import axios from "axios";

const base_url = "http://localhost:12444";
const base_sec_url =  "http://localhost:12555";


export interface NameGroup{
    name: string,
    count: number
}

export const getGroupedByName = () => {
    return axios.get(base_url + "/space-marines/group-by-name")
}

export const getStarships = () => {
    return axios.get(base_sec_url + "/starship/")
}

export const createSpaceMarine = (data: string) => {
    return axios.post(base_url + "/space-marines/", data, {
        headers: {
            'Content-Type': 'application/json',
        }
    })
}

export const updateSpaceMarine = (id: number, data: string) => {
    return axios.put(base_url + "/space-marines/" + id, data, {
        headers: {
            'Content-Type': 'application/json',
        }
    })
}

export const getSpaceMarines = () => {
    return axios.get(base_url + "/space-marines/")
}

export const searchSpaceMarines = (request: string) => {
    return axios.get(base_url + "/space-marines/by-filter" + request)
}

export const getMaxHealed = () => {
    return axios.get(base_url + "/space-marines/max-healed")
}

export const deleteSpaceMarine = (id: number) => {
    return axios.delete(base_url + "/space-marines/" + id);
}

export const createStarShip = () => {
    return axios.post(base_sec_url + "/starship/")
}

export const deleteStarShip = (id: number) => {
    return axios.delete(base_sec_url + "/starship/" + id)
}

export const loadToStarship = (smId: number, ssId: number) => {
    return axios.post(base_sec_url + "/starship/" + ssId + "/load/" + smId)
}

export const unloadAllFromStarship = (ssId: number) => {
    return axios.post(base_sec_url + "/starship/" + ssId + "/unload-all")
}