import {MeleeWeapon} from "../spaceMarineTable/spaceMarineCard/SpaceMarine"
import {Button, Checkbox, Chip, NativeSelect, TextField} from "@mui/material";
import React, {useState} from "react";
import {DatePicker, LocalizationProvider} from "@mui/x-date-pickers";
import {AdapterDayjs} from '@mui/x-date-pickers/AdapterDayjs';
import './SpaceMarineSearch.css';
import {Dayjs} from "dayjs";

enum SortDir {
    ASC = "asc",
    DESC = "desc"
}

interface Sort {
    id: number,
    sort: string,
    dir: SortDir
}

const SpaceMarineSearch = (sendRequest: Function) => {
    const [sort, setSort] = useState<string>("id");
    const [sortAsc, setSortDir] = useState<boolean>(false);
    const [sortArray, setSortArray] = useState<Sort[]>([])
    const [id, setId] = useState<number>(NaN);
    const [name, setName] = useState<string>("");
    const [createdBefore, setCreatedBefore] = useState<Dayjs | null>(null);
    const [createdAfter, setCreatedAfter] = useState<Dayjs | null>(null);
    const [xCoordinateGreaterThan, setXCoordinateGreaterThan] = useState<number>(NaN);
    const [xCoordinateLesserThan, setXCoordinateLesserThan] = useState<number>(NaN);
    const [xCoordinateEqual, setXCoordinateEqual] = useState<number>(NaN);
    const [yCoordinateGreaterThan, setYCoordinateGreaterThan] = useState<number>(NaN);
    const [yCoordinateLesserThan, setYCoordinateLesserThan] = useState<number>(NaN);
    const [yCoordinateEqual, setYCoordinateEqual] = useState<number>(NaN);
    const [healthGreaterThan, setHealthGreaterThan] = useState<number>(NaN);
    const [healthLesserThan, setHealthLesserThan] = useState<number>(NaN);
    const [healthEqual, setHealthEqual] = useState<number>(NaN);
    const [hearthCountGreaterThan, setHearthCountGreaterThan] = useState<number>(NaN);
    const [hearthCountLesserThan, setHearthCountLesserThan] = useState<number>(NaN);
    const [hearthCountEqual, setHearthCountEqual] = useState<number>(NaN);
    const [loyal, setLoyal] = useState<boolean>(false);
    const [loyalUsed, setLoyalUsed] = useState<boolean>(false);
    const [meleeWeapon, setMeleeWeapon] = useState<MeleeWeapon>(MeleeWeapon.MANREAPER);
    const [meleeWeaponUsed, setMeleeWeaponUsed] = useState<boolean>(false);
    const [chapterName, setChapterName] = useState<string>("");
    const [chapterWorld, setChapterWorld] = useState<string>("")
    const [chipCounter, setChipCounter] = useState<number>(0);

    const sendFilter = () => {
        let params = "";
        sortArray.forEach((v) =>  params += "&sort=" + v.sort + "," + v.dir);
        if(!isNaN(id) && id.toString() !== '') params += "&id="  + id;
        if(name !== null && name !== '') params += "&name=" + name;
        if(createdBefore !== null) params += "&createdBefore=" + createdBefore.toISOString();
        if(createdAfter !== null) params += "&createdAfter=" + createdAfter.toISOString();
        if(xCoordinateLesserThan.toString() !== '' && !isNaN(xCoordinateLesserThan)) params += "&xCoordinateLesserThan=" + xCoordinateLesserThan;
        if(xCoordinateGreaterThan.toString() !== '' && !isNaN(xCoordinateGreaterThan)) params += "&xCoordinateGreaterThan=" + xCoordinateGreaterThan;
        if(xCoordinateEqual.toString() !== '' && !isNaN(xCoordinateEqual)) params += "&xCoordinateEqual=" + xCoordinateEqual;
        if(yCoordinateLesserThan.toString() !== '' && !isNaN(yCoordinateLesserThan)) params += "&yCoordinateLesserThan=" + yCoordinateLesserThan;
        if(yCoordinateGreaterThan.toString() !== '' && !isNaN(yCoordinateGreaterThan)) params += "&yCoordinateGreaterThan=" + yCoordinateGreaterThan;
        if(yCoordinateEqual.toString() !== '' && !isNaN(yCoordinateEqual)) params += "&yCoordinateEqual=" + yCoordinateEqual;
        if(healthLesserThan.toString() !== '' && !isNaN(healthLesserThan)) params += "&healthLesserThan=" + healthLesserThan;
        if(healthGreaterThan.toString() !== '' && !isNaN(healthGreaterThan)) params += "&healthGreaterThan=" + healthGreaterThan;
        if(healthEqual.toString() !== '' && !isNaN(healthEqual)) params += "&healthEqual=" + healthEqual;
        if(hearthCountLesserThan.toString() !== '' && !isNaN(hearthCountLesserThan)) params += "&heartCountLesserThan=" + hearthCountLesserThan;
        if(hearthCountGreaterThan.toString() !== '' && !isNaN(hearthCountGreaterThan)) params += "&heartCountGreaterThan=" + hearthCountGreaterThan;
        if(hearthCountEqual.toString() !== '' && !isNaN(hearthCountEqual)) params += "&heartCountEqual=" + hearthCountEqual;
        if(loyalUsed) params += "&loyal=" + loyal;
        if(meleeWeaponUsed) params += "&meleeWeapon=" + meleeWeapon;
        if(chapterName !== '') params += "&chapterName=" + chapterName;
        if(chapterWorld !== '') params += "&chapterWorld=" + chapterWorld;
        sendRequest(params)
    }

    const setIdVal = (id: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        setId(id.target.value as unknown as number)
    }

    const setXGreaterThanVal = (xEvent: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        let xGreaterThan = xEvent.target.value as unknown as number
        console.log(id.toString() === '' || isNaN(id))
        if (xGreaterThan === xCoordinateLesserThan){
            setXEquals(xEvent);
        }
        else{
            setXCoordinateGreaterThan(xGreaterThan);
        }
    }

    const setXLesserThanVal = (xEvent: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        let xLesserThan = xEvent.target.value as unknown as number
        if (xCoordinateGreaterThan === xLesserThan){
            setXEquals(xEvent);
        }
        else{
            setXCoordinateLesserThan(xLesserThan);
        }
    }

    const setXEquals = (xEquals: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        setXCoordinateEqual(xEquals.target.value as unknown as number)
    }

    const setYGreaterThanVal = (yEvent: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        let yGreaterThan = yEvent.target.value as unknown as number
        if (yGreaterThan === yCoordinateLesserThan){
            setYEquals(yEvent);
        }
        else{
            setYCoordinateGreaterThan(yGreaterThan);
        }
    }

    const setYLesserThanVal = (yEvent: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        let yLesserThan = yEvent.target.value as unknown as number
        if (yCoordinateGreaterThan === yLesserThan){
            setYEquals(yEvent);
        }
        else{
            setYCoordinateLesserThan(yLesserThan);
        }
    }

    const setYEquals = (yEquals: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        setYCoordinateEqual(yEquals.target.value as unknown as number)
    }

    const setHealthGreaterThanVal = (healthEvent: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        let healthGreaterThan = healthEvent.target.value as unknown as number
        if (healthGreaterThan === healthLesserThan){
            setHealthEquals(healthEvent);
        }
        else{
            setHealthGreaterThan(healthGreaterThan);
        }
    }

    const setHealthLesserThanVal = (healthEvent: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        let healthLesserThan = healthEvent.target.value as unknown as number
        if (healthGreaterThan === healthLesserThan){
            setHealthEquals(healthEvent);
        }
        else{
            setHealthLesserThan(healthLesserThan);
        }
    }

    const setHealthEquals = (healthEvent: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        setHealthEqual(healthEvent.target.value as unknown as number);
    }

    const setHearthCountGreaterThanVal = (hearthCountEvent: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        let hearthCountGreaterThan = hearthCountEvent.target.value as unknown as number
        if (hearthCountGreaterThan === hearthCountLesserThan){
            setHearthCountEquals(hearthCountEvent);
        }
        else{
            setHearthCountGreaterThan(hearthCountGreaterThan);
        }
    }

    const setHearthCountLesserThanVal = (hearthCountEvent: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        let hearthCountLesserThan = hearthCountEvent.target.value as unknown as number
        if (hearthCountGreaterThan === hearthCountLesserThan){
            setHearthCountEquals(hearthCountEvent);
        }
        else{
            setHearthCountLesserThan(hearthCountLesserThan);
        }
    }

    const setHearthCountEquals = (healthEquals: React.ChangeEvent<HTMLTextAreaElement | HTMLInputElement>) => {
        setHearthCountEqual(healthEquals.target.value as unknown as number)
    }

    const setLoyalVal = () => {
        setLoyal(!loyal);
    }

    const setLoyalUsedVal = () => {
        setLoyalUsed(!loyalUsed);
    }

    const setMeleeWeaponVal = (meleeWeapon: React.ChangeEvent<HTMLSelectElement>) => {
        setMeleeWeapon(meleeWeapon.target.value as MeleeWeapon);
    }

    const setMeleeWeaponUsedVal = () => {
        setMeleeWeaponUsed(!meleeWeaponUsed);
    }

    const setChapterNameVal = (chapterName: string) => {
        setChapterName(chapterName);
    }

    const setChapterWorldVal = (chapterWorld: string) => {
        setChapterWorld(chapterWorld);
    }

    const addChip = () => {
        const newChip: Sort = {id: chipCounter, sort: sort, dir: sortAsc ? SortDir.ASC : SortDir.DESC};
        if (sortArray.filter(v => v.sort === newChip.sort).length === 0) {
            setChipCounter(() => chipCounter + 1);
            setSortArray(() => [...sortArray, newChip]);
        }
    }

    const deleteChip = (id: number) => {
        setSortArray(arr => arr.filter(item => {console.log(item.id); return item.id !== id}))
    }

    return (
        <div className="searchWrapper">
        <div className="SpaceMarineSearch">
            <div className="FilterItem">
                <span>Add sort:</span>
                <NativeSelect id="sortFilter" value={sort} onChange={(e) => setSort(e.target.value)}>
                    <option value="id">ID</option>
                    <option value="name">Name</option>
                    <option value="hearth_count">Hearth count</option>
                    <option value="health">Health</option>
                    <option value="x_coordinate">X coordinate</option>
                    <option value="y_coordinate">Y coordinate</option>
                    <option value="chapter_name">Chapter name</option>
                    <option value="chapter_world">Chapter world</option>
                </NativeSelect>
                <span>Ascending:</span>
                <Checkbox id="sortAscFilter" checked={sortAsc}  onClick={() => setSortDir(!sortAsc)}/>
                <Button id="addChip" className="btn" onClick={addChip}>Add</Button>
            </div>
            <div className="FilterItem">
                <span>Sorts:</span>
                {sortArray.map(v => <Chip label={v.sort} color={v.dir === SortDir.ASC ? "success" : "primary"} onDelete={() => deleteChip(v.id)}/>)}
            </div>
            <div className="FilterItem">
                <span>Name:</span>
                <TextField id="nameFilter" label="Name" variant="outlined"
                           onChange={(e) => setName(e.target.value)}/>
            </div>
            <div className="FilterItem">
                <span>Space Marine id:</span>
            <TextField id="idSearchInput" label="Id" variant="outlined" type="number"
                       onChange={(e) => setIdVal(e)}/>
            </div>
            <div className="FilterItem">
                <span>Creation Date:</span>
            <LocalizationProvider dateAdapter={AdapterDayjs}>
                <DatePicker
                    label="Created after"
                    value={createdAfter}
                    onChange={(newValue) => {
                        setCreatedAfter(newValue);
                    }}
                    renderInput={(params) => <TextField {...params} />}
                />
                <DatePicker
                    label="Created before"
                    value={createdBefore}
                    onChange={(newValue) => {
                        setCreatedBefore(newValue);
                    }}
                    renderInput={(params) => <TextField {...params} />}
                />
            </LocalizationProvider>
            </div>
            <div className="FilterItem">
                <span>X coordinate lesser than:</span>
            <TextField type="number" label="xCoordinateLesserThan" id="xCoordinateLesserThanInput" variant="outlined"
                onChange={(e) => setXLesserThanVal(e)}/>
            </div>
            <div className="FilterItem">
                <span>X coordinate greater than:</span>
                <TextField type="number" label="xCoordinateGreaterThan" id="xCoordinateGreaterThanInput" variant="outlined"
                           onChange={(e) => setXGreaterThanVal(e)}/>
            </div>
            <div className="FilterItem">
                <span>Y coordinate lesser than:</span>
                <TextField type="number" label="yCoordinateLesserThan" id="yCoordinateLesserThanInput" variant="outlined"
                           onChange={(e) => setYLesserThanVal(e)}/>
            </div>
            <div className="FilterItem">
                <span>Y coordinate greater than:</span>
                <TextField type="number" label="yCoordinateGreaterThan" id="yCoordinateGreaterThanInput" variant="outlined"
                           onChange={(e) => setYGreaterThanVal(e)}/>
            </div>
            <div className="FilterItem">
                <span>Health lesser than:</span>
                <TextField type="number" label="healthLesserThan" id="healthLesserThanInput" variant="outlined"
                           onChange={(e) => setHealthLesserThanVal(e)}/>
            </div>
            <div className="FilterItem">
                <span>Health greater than:</span>
                <TextField type="number" label="healthGreaterThan" id="healthGreaterThanInput" variant="outlined"
                           onChange={(e) => setHealthGreaterThanVal(e)}/>
            </div>
            <div className="FilterItem">
                <span>Hearth count lesser than:</span>
                <TextField type="number" label="hearthCountLesserThan" id="hearthCountLesserThanInput" variant="outlined"
                           onChange={(e) => setHearthCountLesserThanVal(e)}/>
            </div>
            <div className="FilterItem">
                <span>Hearth count greater than:</span>
                <TextField type="number" label="hearthCountGreaterThan" id="hearthCountGreaterThanInput" variant="outlined"
                           onChange={(e) => setHearthCountGreaterThanVal(e)}/>
            </div>
            <div className="FilterItem">
                <span>Loyal used:</span>
                <Checkbox id="loyalUsedFilter" checked={loyalUsed}  onClick={setLoyalUsedVal}/>
            </div>
            <div className="FilterItem">
                <span>Loyal:</span>
                <Checkbox id="loyalFilter" checked={loyal} disabled={!loyalUsed} onChange={setLoyalVal}/>
            </div>
            <div className="FilterItem">
                <span>Melee weapon used:</span>
                <Checkbox id="meleeWeaponUsedFilter" checked={meleeWeaponUsed}  onClick={setMeleeWeaponUsedVal}/>
            </div>
            <div className="FilterItem">
                <span>Melee weapon:</span>
                <NativeSelect id="meleeWeaponFilter" value={meleeWeapon} disabled={!meleeWeaponUsed} onChange={(e) => setMeleeWeaponVal(e)}>
                    <option value={MeleeWeapon.MANREAPER}>Manreaper</option>
                    <option value={MeleeWeapon.CHAIN_AXE}>Chain axe</option>
                    <option value={MeleeWeapon.CHAIN_SWORD}>Chain Sword</option>
                    <option value={MeleeWeapon.LIGHTING_CLAW}>Lightning Claw</option>
                    <option value={MeleeWeapon.POWER_BLADE}>Power blade</option>
                </NativeSelect>
            </div>
            <div className="FilterItem">
                <span>Chapter name:</span>
                <TextField id="chapterNameFilter" label="Chapter name" variant="outlined"
                           onChange={(e) => setChapterNameVal(e.target.value)}/>
            </div>
            <div className="FilterItem">
                <span>Chapter world:</span>
                <TextField id="chapterWorldFilter" label="Chapter world" variant="outlined"
                           onChange={(e) => setChapterWorldVal(e.target.value)}/>
            </div>
        </div>
    <Button id="searchFilter" className="btn" onClick={sendFilter}>Search</Button>
    </div>
    )
}

export default SpaceMarineSearch