import React, {useState} from "react";
import './HomePage.css'
import {Box, Tab, Tabs, Typography} from "@mui/material";
import {SpaceMarinePage} from "../SpaceMarinePage/SpaceMarinePage";
import "../api/Api"
import {SpaceShipPage} from "../SpaceshipPage/SpaceShipPage";


interface TabPanelProps {
    children?: React.ReactNode | React.ReactNode[]
    index: number;
    value: number;
}


function a11yProps(index: number) {
    return {
        id: `simple-tab-${index}`,
        'aria-controls': `simple-tabpanel-${index}`,
    };
}

function TabPanel(props: TabPanelProps) {
    const { children, value, index, ...other } = props;

    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`simple-tabpanel-${index}`}
            aria-labelledby={`simple-tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
}


function HomePage() {

    const [value, setValue] = useState(0);


    const handleChange = (event: React.SyntheticEvent, newValue: number) => {
        setValue(newValue);
    };

    return (
        <div className="SpaceMarineHomePage">
            <Box sx={{width: '100%'}}>
                <Box sx={{borderBottom: 1, borderColor: 'divider'}}>
                    <Tabs value={value} onChange={handleChange} aria-label="Menu">
                        <Tab label="Space Marines" {...a11yProps(0)} />
                        <Tab label="Spaceships" {...a11yProps(1)} />
                    </Tabs>
                </Box>
                <TabPanel value={value} index={0}>
                    {SpaceMarinePage()}
                </TabPanel>
                <TabPanel value={value} index={1}>
                    {SpaceShipPage()}
                </TabPanel>
            </Box>
        </div>
    );
}

export default HomePage