import {Button, TextField} from "@mui/material";
import React from "react";
import "./SpaceShip.css";

export type StarShipInfo = {
    id: number,
    spaceMarines: number[],
    marineToLoad: number
}

const SpaceShip = (starship: StarShipInfo, change: Function, load: Function, unload: Function, deleteStarShip: Function) => {

    return (
        <div className="SpaceShipCard">
            <span className="SpaceShipId">{starship.id}</span>
            <div className="SpaceShipMarineListWrapper">
                Space Marines:
            </div>
            <div className="SpaceShipMarineList">
                {starship.spaceMarines.map(value =>
                    <div className="SpaceMarineInStarShip">{value}</div>
                )}
            </div>
            <Button id={"delBtn" + starship.id} className="btn" onClick={() => deleteStarShip(starship.id)}>Delete</Button>
            <Button id={"unloadBtn" + starship.id} disabled={starship.spaceMarines.length === 0} className="btn" onClick={() => unload(starship.id)}>Unload all</Button>
            <Button id={"loadBtn" + starship.id} disabled={isNaN(starship.marineToLoad)} className="btn" onClick={() => load(starship.id)}>Load</Button>
            <TextField type="number" label="Space Marine id" id={"smId" + starship.id} variant="standard"
                       onChange={(e) => change(starship.id,e.target.value)}/>
        </div>
    )
}

export default SpaceShip;