import SpaceMarine, {SpaceMarineInfo} from "./spaceMarineCard/SpaceMarine";
import {Button, TextField} from "@mui/material";
import React, {useEffect, useState} from "react";
import "./SpaceMarineTable.css";


export const SpaceMarineTable = (spaceMarines: SpaceMarineInfo[], maxPage: number, setEdit: Function, reloadPage: Function, changePage: Function) => {

    const [currentPage, setCurrentPage] = useState<number>(1);
    const [tempPage, setTempPage] = useState(1);

    useEffect(() => setTempPage(currentPage), [currentPage])

    return (
        <div className="SpaceMarineTable">
            <div className="SpaceMarineList">
            {spaceMarines.map((v) => SpaceMarine(v, setEdit, reloadPage))}
            </div>
            <div className="SpaceMarineTableNav">
                <Button id="prevPageBtn" className="btn" disabled={currentPage <= 1} onClick={() => {
                    changePage(parseInt(String(currentPage)) - 1);
                    setCurrentPage(parseInt(String(currentPage)) - 1)
                }}>Prev Page</Button>
            <TextField type="number" value={tempPage} id="currentPageTextInput" variant="standard" size="small"
                       error={tempPage < 1 || tempPage > maxPage}
                       onChange={(e) => setTempPage(parseInt(e.target.value))}
                       onKeyDown={(e) => {
                            if (e.key === 'Enter') {
                                if (tempPage < 1) {
                                    setCurrentPage(1)
                                    changePage(1)
                                }
                                else if (tempPage > maxPage) {
                                    setCurrentPage(maxPage);
                                    changePage(maxPage)
                                }
                                else{
                                    setCurrentPage(tempPage);
                                    changePage(tempPage)
                                }
                            }
                       }
            }/>
            <span> / {maxPage} </span>
                <Button id="nextPageBtn" className="btn" disabled={currentPage >= maxPage} onClick={() => {
                    changePage(parseInt(String(currentPage)) + 1);
                    setCurrentPage(parseInt(String(currentPage)) + 1)
                }}>Next Page</Button>
            </div>
        </div>

    )
}