import React, {useEffect, useState} from "react";
import {MeleeWeapon, SpaceMarineInfo} from "../spaceMarineTable/spaceMarineCard/SpaceMarine";
import {Button, Checkbox, NativeSelect, TextField} from "@mui/material";
import {AdapterDayjs} from "@mui/x-date-pickers/AdapterDayjs";
import {DatePicker, LocalizationProvider} from "@mui/x-date-pickers";
import './SpaceMarineEditor.css'
import "../../api/Api"
import {createSpaceMarine, updateSpaceMarine} from "../../api/Api";
import {AdapterMoment} from "@mui/x-date-pickers/AdapterMoment";

const SpaceMarineEditor = (state: SpaceMarineInfo, reload: Function, close: Function) => {
    const [id, setId] = useState<number>(state.id);
    const [name, setName] = useState<string>( state.name);
    const [creationDate, setCreationDate] = useState<string>(state.creationDate)
    const [xCoordinate, setXCoordinate] = useState<number>(state.xcoordinate)
    const [yCoordinate, setYCoordinate] = useState<number>(state.ycoordinate)
    const [health, setHealth] = useState<number>(state.health)
    const [hearthCount, setHearthCount] = useState<number>(state.heartCount)
    const [loyal, setLoyal] = useState<boolean>(state.loyal)
    const [meleeWeapon, setMeleeWeapon] = useState<MeleeWeapon>(state.meleeWeapon)
    const [chapterName, setChapterName] = useState<string>(state.chapterName)
    const [chapterWorld, setChapterWorld] = useState<string>(state.chapterWorld)
    const [isValid, setIsValid] = useState<boolean>(false);

    const reg = /\[.*]/

    useEffect(() => { setId(state.id)}, [state.id] )
    useEffect(() => { setName(state.name)}, [state.name] )
    useEffect(() => { setCreationDate(state.creationDate.replace(reg,''))}, [state.creationDate])
    useEffect(() => { setXCoordinate(state.xcoordinate)}, [state.xcoordinate] )
    useEffect(() => { setYCoordinate(state.ycoordinate)}, [state.ycoordinate] )
    useEffect(() => { setHealth(state.health)}, [state.health] )
    useEffect(() => { setHearthCount(state.heartCount)}, [state.heartCount] )
    useEffect(() => { setLoyal(state.loyal)}, [state.loyal] )
    useEffect(() => { setMeleeWeapon(state.meleeWeapon)}, [state.meleeWeapon] )
    useEffect(() => { setChapterName(state.chapterName == null ? "" : state.chapterName)}, [state.chapterName] )
    useEffect(() => { setChapterWorld(state.chapterWorld == null ? "" : state.chapterWorld)}, [state.chapterWorld] )

    React.useEffect(() => {
        setIsValid(!(name === '' ||
            (xCoordinate.toString() === '' || xCoordinate <= -282) ||
            yCoordinate.toString() === '' || (health.toString() === '' || health <= 0) ||
            (hearthCount.toString() !== "1" && hearthCount.toString() !== "2" && hearthCount.toString() !== "3") ||
            (chapterWorld !== '' && chapterName === '')));
    }, [name, xCoordinate, yCoordinate, health, hearthCount, chapterName, chapterWorld]);

    const send = () => {
        let data = JSON.stringify({
            id: id,
            name: name,
            xcoordinate: xCoordinate,
            ycoordinate: yCoordinate,
            health: health,
            heartCount: hearthCount,
            loyal: loyal,
            meleeWeapon: meleeWeapon === MeleeWeapon.NONE ? null : meleeWeapon,
            chapterName: chapterName === "" ? null : chapterName,
            chapterWorld: chapterWorld === "" ? null : chapterWorld
        })
        updateSpaceMarine(id, data).then(() => {reload(); close()})
    }

    return (
        <div className="SpaceMarineEditor">
            <ul>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Id:</span>
                    <TextField type="number" disabled={true} value={id} label="Id" id="idEditor" variant="outlined"
                               onChange={(e) => setId(parseInt(e.target.value))}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Name:</span>
                    <TextField type="text" error={name === ''} value={name} label="Name" id="nameEditor" variant="outlined"
                               onChange={(e) => setName(e.target.value)}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Creation date:</span>
                <LocalizationProvider dateAdapter={AdapterMoment}>
                    <DatePicker
                        label="Creation Date"
                        value={creationDate}
                        disabled={true}
                        onChange={(newValue) => {
                            setCreationDate(newValue == null ? "" : newValue);
                        }}
                        renderInput={(params) => <TextField {...params} />}
                    />
                </LocalizationProvider>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">X coordinate:</span>
                    <TextField type="number" error={isNaN(xCoordinate) || xCoordinate <= -282}  value={state?.xcoordinate} label="X coordinate" id="xCoordinateEditor" variant="outlined"
                               onChange={(e) => setXCoordinate(parseInt(e.target.value))}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Y coordinate:</span>
                    <TextField type="number" error={isNaN(yCoordinate)} value={yCoordinate} label="Y coordinate" id="yCoordinateEditor" variant="outlined"
                               onChange={(e) => setYCoordinate(parseInt(e.target.value))}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Health:</span>
                    <TextField type="number" error={isNaN(health) || health <= 0} value={health} label="Health" id="healthEditor" variant="outlined"
                               onChange={(e) => setHealth(parseInt(e.target.value))}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Hearth count:</span>
                    <TextField type="number" error={hearthCount > 3 || hearthCount < 1} value={hearthCount} label="Hearth count" id="hearthCountEditor" variant="outlined"
                               onChange={(e) => setHearthCount(parseInt(e.target.value))}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Loyal:</span>
                    <Checkbox id="loyalEditor" checked={loyal}  onClick={() => setLoyal(!loyal)}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Melee weapon:</span>
                    <NativeSelect id="meleeWeaponFilter" value={meleeWeapon} onChange={(e) => setMeleeWeapon(e.target.value as MeleeWeapon)}>
                        <option value={MeleeWeapon.NONE}>None</option>
                        <option value={MeleeWeapon.MANREAPER}>Manreaper</option>
                        <option value={MeleeWeapon.CHAIN_AXE}>Chain axe</option>
                        <option value={MeleeWeapon.CHAIN_SWORD}>Chain Sword</option>
                        <option value={MeleeWeapon.LIGHTING_CLAW}>Lightning Claw</option>
                        <option value={MeleeWeapon.POWER_BLADE}>Power blade</option>
                    </NativeSelect>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Chapter name:</span>
                    <TextField type="text" error={chapterWorld !== '' && chapterName === ''} value={chapterName} label="Chapter name" id="chapterNameEditor" variant="outlined"
                               onChange={(e) => setChapterName(e.target.value)}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Chapter world:</span>
                    <TextField type="text" value={chapterWorld} label="Chapter world" id="chapterWorldEditor" variant="outlined"
                               onChange={(e) => setChapterWorld(e.target.value)}/>
                </li>
                <Button className="btn" disabled={!isValid} id="sendEditor" onClick={send}>Send</Button>
            </ul>
        </div>
    )
}

export default SpaceMarineEditor