import SpaceMarineSearch from "./spaceMarineSearch/SpaceMarineSearch";
import {Button, Dialog, DialogActions, DialogContent, DialogTitle} from "@mui/material";
import SpaceMarineEditor from "./spaceMarineEditor/SpaceMarineEditor";
import React, {useEffect, useState} from "react";
import {MeleeWeapon, SpaceMarineInfo} from "./spaceMarineTable/spaceMarineCard/SpaceMarine";
import {SpaceMarineTable} from "./spaceMarineTable/SpaceMarineTable";
import {getGroupedByName, getMaxHealed, NameGroup, searchSpaceMarines} from "../api/Api";
import "./SpaceMarinePage.css";
import SpaceMarineCreator from "./spaceMarineEditor/SpaceMarineCreator";


export const SpaceMarinePage = () => {

    const [spaceMarines, setSpaceMarines] = useState<SpaceMarineInfo[]>([]);
    const [open, setOpen] = useState(false);
    const [maxPage, setMaxPage] = useState(1);
    const [page, setPage] = useState(1);
    const [editOpen, setEditOpen] = useState(false);
    const [currentEdit, setCurrentEdit] = useState<SpaceMarineInfo>({id: 0, name: "",xcoordinate: 0,
        ycoordinate: 0, health: 0, heartCount: 0, creationDate: "", chapterWorld: "", chapterName: "",
        loyal: false, meleeWeapon: MeleeWeapon.NONE});
    const [groupedOpen, setGroupedOpen] = useState<boolean>(false);
    const [group, setGroups] = useState<NameGroup[]>([]);
    const [filterParams, setFilterParams] = useState("")

    const size = 3;

    useEffect(() => {
        //setSpaceMarines([{id: 1, name: "asa", chapterName: "das", loyal: true, health: 1, meleeWeapon: MeleeWeapon.MANREAPER, heartCount: 3, chapterWorld: "Kurava", ycoordinate: 11, xcoordinate: 12, creationDate: Date.now().toString()}])
        reloadPage()
    }, [])

    useEffect(() => reloadPage(), [page])

    const sendSearchRequest = (params: string) => {
        setFilterParams(params);
        searchSpaceMarines("?" + "page=" + page + "&size=" + size + params).then(r => {
            setPage(1);
            setSpaceMarines(r.data.list);
            let pageCount = Math.floor(r.data.count / size);
            if (r.data.count % size > 0 ) pageCount++;
            if (pageCount === 0) pageCount++;
            setMaxPage(pageCount)
        })
    }

    const reloadPage = () => {
        console.log(filterParams)
        searchSpaceMarines("?" + "page=" + page + "&size=" + size + filterParams).then(r => {
            setSpaceMarines(r.data.list);
            let pageCount = Math.floor(r.data.count / size);
            if (r.data.count % size > 0 ) pageCount++;
            if (pageCount === 0) pageCount++;
            setMaxPage(pageCount)
        })
    }

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    const handleEditOpen = () => {
        setEditOpen(true);
    }

    const handleEditClose = () => {
        setEditOpen(false);
    }

    const handleGroupedOpen = () => {
        getGrouped();
        setGroupedOpen(true);
    }

    const handleGroupedClosed = () => {
        setGroupedOpen(false);
    }

    const setEdit = (info: SpaceMarineInfo) => {
        setCurrentEdit(info);
        handleEditOpen();
    }

    const renderEdit = () => {
        return SpaceMarineEditor(currentEdit, reloadPage, handleEditClose);
    }

    const getMaxHealth = () => {
        getMaxHealed().then(r => setSpaceMarines([r.data]));
    }

    const changePage = (newPage: number) => {
        if (newPage <= maxPage && newPage >= 1){
            setPage(newPage);
        }
    }

    const getGrouped = () => {
        getGroupedByName().then(r => setGroups(r.data))
    }

        return (
            <div className="SpaceMarinePage">
                <div className="spaceMarineNav">
                    {SpaceMarineSearch(sendSearchRequest)}
                    <Button id="MaxHealedBtn" className="btn" onClick={getMaxHealth}>Max Healed</Button>
                    <Button className="btn" id="createSMBtn" onClick={handleClickOpen}>Add Space Marine</Button>
                    <Button className="btn" id="getGroupedByName" onClick={handleGroupedOpen}>Grouped by name</Button>
                    <Dialog open={editOpen} onClose={handleEditClose}>
                        <DialogTitle>Space Marine edit</DialogTitle>
                        <DialogContent>
                            {renderEdit()}
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleEditClose}>Cancel</Button>
                        </DialogActions>
                    </Dialog>
                    <Dialog open={groupedOpen} onClose={handleGroupedClosed}>
                        <DialogTitle>Space Marines grouped by name</DialogTitle>
                        <DialogContent>
                            <div className="GroupsList">
                                {group.map((v) => <div><span>{v.name} : {v.count}</span></div>)}
                            </div>
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleGroupedClosed}>Cancel</Button>
                        </DialogActions>
                    </Dialog>
                </div>
                <div className="spaceMarineTable">
                    {SpaceMarineTable(spaceMarines, maxPage, setEdit, reloadPage, changePage)}
                    <Dialog open={open} onClose={handleClose}>
                        <DialogTitle>Space Marine creation</DialogTitle>
                        <DialogContent>
                            {SpaceMarineCreator(reloadPage, handleClose)}
                        </DialogContent>
                        <DialogActions>
                            <Button onClick={handleClose}>Cancel</Button>
                        </DialogActions>
                    </Dialog>
                </div>
            </div>
    );
}