import {Button, TextField} from "@mui/material";
import React from "react";
import "./SpaceMarine.css"
import axios from "axios";
import "../../../api/Api";
import {deleteSpaceMarine} from "../../../api/Api";
import {AdapterMoment} from "@mui/x-date-pickers/AdapterMoment";
import {DatePicker, LocalizationProvider} from "@mui/x-date-pickers";
import moment from "moment/moment";

export enum MeleeWeapon {
    NONE = "null",
    CHAIN_SWORD = "CHAIN_SWORD",
    CHAIN_AXE = "CHAIN_AXE",
    MANREAPER = "MANREAPER",
    LIGHTING_CLAW = "LIGHTING_CLAW",
    POWER_BLADE = "POWER_BLADE"
}

export type SpaceMarineInfo = {
    id: number,
    name: string,
    xcoordinate: number,
    ycoordinate: number,
    creationDate: string,
    health: number,
    heartCount: number,
    loyal: boolean,
    meleeWeapon: MeleeWeapon,
    chapterName: string,
    chapterWorld: string
}

const SpaceMarine = (props: SpaceMarineInfo, setEdit: Function, reloadFunc: Function) => {

    const reg = /\[.*]/
    const time = moment(props.creationDate.replace(reg, ''))

    const delSpaceMarine = () => {
        deleteSpaceMarine(props.id).then(() => reloadFunc());
    }
    return (
        <div className="SpaceMarineCard">
            <div className="spaceMarineLeftWrapper">
            <span>Id: {props.id} </span>
                <br/>
            <span>Name: {props.name}</span>
                <br/>
            <span>Coordinates: {props.xcoordinate}, {props.ycoordinate}</span>
                <br/>
            <span>Health: {props.health}</span>
            <br/>
            <span>Creation date: {time.format("DD.MM.YYYY HH.MM.SS")} </span>
            <br/>
            <Button id={"editBtn" + props.id} className="btn" onClick={() => setEdit(props)}>Edit</Button>
            </div>
            <div className="spaceMarineRightWrapper">
            <span>Heart count: {props.heartCount}</span>
                <br/>
            <span>Loyal: {props.loyal ? "true" : "false"}</span>
                <br/>
            <span>Melee Weapon: {props.meleeWeapon}</span>
                <br/>
            <span>Chapter: {props.chapterName}, {props.chapterWorld}</span>
                <br/>
                <br/>
            <Button id={"deleteBtn" + props.id} className="btn" onClick={delSpaceMarine}>Delete</Button>
            </div>
        </div>
    );
}

export default SpaceMarine