import React, {useState} from "react";
import {MeleeWeapon} from "../spaceMarineTable/spaceMarineCard/SpaceMarine";
import {Button, Checkbox, NativeSelect, TextField} from "@mui/material";
import './SpaceMarineEditor.css'
import "../../api/Api"
import {createSpaceMarine} from "../../api/Api";

const SpaceMarineEditor = (reload: Function, close: Function) => {
    const [name, setName] = useState<string>("");
    const [xCoordinate, setXCoordinate] = useState<string>("")
    const [yCoordinate, setYCoordinate] = useState<string>("")
    const [health, setHealth] = useState<string>("")
    const [hearthCount, setHearthCount] = useState<string>("")
    const [loyal, setLoyal] = useState<boolean>(false)
    const [meleeWeapon, setMeleeWeapon] = useState<MeleeWeapon>(MeleeWeapon.NONE)
    const [chapterName, setChapterName] = useState<string>("")
    const [chapterWorld, setChapterWorld] = useState<string>("")
    const [isValid, setIsValid] = useState<boolean>(false);

    const clear = () => {
        setName('')
        setXCoordinate('')
        setYCoordinate('')
        setHealth('')
        setHearthCount('')
        setLoyal(false)
        setMeleeWeapon(MeleeWeapon.NONE)
        setChapterWorld('')
        setChapterName('')
    }

    React.useEffect(() => {
        setIsValid(!(name === '' ||
            (xCoordinate === '' || parseFloat(xCoordinate) <= -282) ||
            yCoordinate === '' || (health === '' || parseFloat(health) <= 0) ||
            (hearthCount !== "1" && hearthCount !== "2" && hearthCount !== "3") ||
            (chapterWorld !== '' && chapterName === '')));
    }, [name, xCoordinate, yCoordinate, health, hearthCount, chapterName, chapterWorld]);

    const send = () => {
        let data = JSON.stringify({
            name: name,
            xcoordinate: parseInt(xCoordinate),
            ycoordinate: parseInt(yCoordinate),
            health: parseInt(health),
            heartCount: parseInt(hearthCount),
            loyal: loyal,
            meleeWeapon: meleeWeapon === MeleeWeapon.NONE ? null : meleeWeapon,
            chapterName: chapterName === "" ? null : chapterName,
            chapterWorld: chapterWorld === "" ? null : chapterWorld
        })
        createSpaceMarine(data).then(() => {reload();close()})
        clear()
    }

    return (
        <div className="SpaceMarineEditor">
            <ul>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Name:</span>
                    <TextField type="text" error={name === ''} value={name} label="Name" id="nameEditor" variant="outlined"
                               onChange={(e) => setName(e.target.value)}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">X coordinate:</span>
                    <TextField type="number" error={xCoordinate === '' || parseFloat(xCoordinate) <= -282}  value={xCoordinate} label="X coordinate" id="xCoordinateEditor" variant="outlined"
                               onChange={(e) => setXCoordinate(e.target.value)}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Y coordinate:</span>
                    <TextField type="number" error={yCoordinate === ''} value={yCoordinate} label="Y coordinate" id="yCoordinateEditor" variant="outlined"
                               onChange={(e) => setYCoordinate(e.target.value)}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Health:</span>
                    <TextField type="number" error={health === '' || parseFloat(health) <= 0} value={health} label="Health" id="healthEditor" variant="outlined"
                               onChange={(e) => setHealth(e.target.value)}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Hearth count:</span>
                    <TextField type="number" error={hearthCount !== "1" && hearthCount !== "2" && hearthCount !== "3"} value={hearthCount} label="Hearth count" id="hearthCountEditor" variant="outlined"
                               onChange={(e) => setHearthCount(e.target.value)}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Loyal:</span>
                    <Checkbox id="loyalEditor" checked={loyal}  onClick={() => setLoyal(!loyal)}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Melee weapon:</span>
                    <NativeSelect id="meleeWeaponFilter" value={meleeWeapon} onChange={(e) => setMeleeWeapon(e.target.value as MeleeWeapon)}>
                        <option value={MeleeWeapon.NONE}>None</option>
                        <option value={MeleeWeapon.MANREAPER}>Manreaper</option>
                        <option value={MeleeWeapon.CHAIN_AXE}>Chain axe</option>
                        <option value={MeleeWeapon.CHAIN_SWORD}>Chain Sword</option>
                        <option value={MeleeWeapon.LIGHTING_CLAW}>Lightning Claw</option>
                        <option value={MeleeWeapon.POWER_BLADE}>Power blade</option>
                    </NativeSelect>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Chapter name:</span>
                    <TextField type="text" error={chapterWorld !== '' && chapterName === ''} value={chapterName} label="Chapter name" id="chapterNameEditor" variant="outlined"
                               onChange={(e) => setChapterName(e.target.value)}/>
                </li>
                <li className="SMEditorField">
                    <span className="SMEditorSpan">Chapter world:</span>
                    <TextField type="text" value={chapterWorld} label="Chapter world" id="chapterWorldEditor" variant="outlined"
                               onChange={(e) => setChapterWorld(e.target.value)}/>
                </li>
                <Button className="btn" disabled={!isValid} id="sendEditor" onClick={send}>Send</Button>
            </ul>
        </div>
    )
}

export default SpaceMarineEditor